import React, { useCallback, useEffect, useState } from 'react';
import Particles from 'react-tsparticles';
import { loadSlim } from 'tsparticles-slim';

const SimpleParticles = () => {
  const particlesInit = useCallback(async (engine) => {
    await loadSlim(engine);
  }, []);

  const [isVisible, setIsVisible] = useState(true);

  const handleScroll = useCallback(() => {
    const scrollPosition = window.scrollY;
    const windowHeight = window.innerHeight;

    if (scrollPosition > windowHeight) {
      setIsVisible(false);  // Skryje stice, pokud uivatel pejde nad prvn strnku
    } else {
      setIsVisible(true);   // Zobraz stice na prvn strnce
    }
  }, []);

  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [handleScroll]);

  return (
    <div style={{  position: 'absolute', width: '100%', height: '100vh', overflow: 'hidden', zIndex: '1'}}>
      {isVisible && (
        <Particles
          id="tsparticles"
          init={particlesInit}
          options={{
            fullScreen: { enable: false }, // Efekt je omezen na vku a ku divu
            particles: {
              number: {
                value: 20, // Poet komet
                density: {
                  enable: true,
                  value_area: 500, // Rozloen komet
                },
              },
              fpsLimit: 120,
              move: {
                enable: true,
                speed: 5, // Rychlost komet
                direction: "right", // Smr pohybu komet
                random: true,
                straight: false, // Komety se nebudou pohybovat pmo, ale mohou se mrn vlnit
                out_mode: "out", // Komety zmiz, kdy dolet mimo obrazovku
                attract: {
                  enable: false,
                },
              },
              shape: {
                type: "circle", // Tvar komet (kruh)
              },
              size: {
                value: 3, // Velikost komet
                random: true,
              },
              opacity: {
                value: 1, // Poten prhlednost komet
                animation: {
                  enable: true,
                  speed: 1, // Rychlost zmny opacity
                  minimumValue: 0, // Minimln prhlednost pi animaci
                  startValue: "max",
                  sync: false,
                },
              },
              color: {
                value: "#ffffff", // Barva komet
              },
            },
            interactivity: {
              events: {
                onHover: {
                  enable: false, // Interaktivita pi pejet my
                },
                onClick: {
                  enable: false, // Interaktivita pi kliknut
                },
              },
            },
            retina_detect: true,
          }}
          style={{
            position: 'absolute',
            top: 0,
            left: 0,
            width: '100%',
            height: '100%',
          }}
        />
      )}
    </div>
  );
};

export default SimpleParticles;
