import './Aboutme.css'
import Me2 from '../Assets/me.webp';
import Work from '../Assets/work.png';
import CodeService from '../Assets/codeservice.png';

const Aboutme = () => {
    return (
        <div className='Aboutme' id='Aboutme'>
            <p className='Aboutme-Title'>O mn</p>
            <div className='Aboutme-content'>
                
                <div className='Aboutme-Left'>
                    <p className='Aboutme-Paragraph'>
                        Jmenuji se Daniel Tborsk, je mi 21 let a jsem FULL-STACK webov vvoj.
                        Vystudoval jsem Stedn prmyslovou kolu v Ostrov a te se
                        vnuji designu, programovn a sprv webovch strnek/aplikac.
                    </p>
                    <p className='Aboutme-Paragraph'>
                        Mm za sebou adu projekt, kter si mete prohldnout v mm portfoliu a pokud Vs zaujme m prce, 
                        mohu tak Vm pomoci s realizac Vaeho projektu. Sta si zadat o nkterou z mch slueb nebo m zkontaktovat...
                    </p>
                    <div className='Aboutme-Buttons'>
                        <a href="#Portfolio"><button className='Aboutme-Portfolio-Button'><img src={Work} alt="My work" /> Portfolio</button></a>
                        <a href="#Services"><button className='Aboutme-Services-Button'><img src={CodeService} alt="Services" /> Sluby</button></a>
                    </div>
                </div>
                <div className='Aboutme-Right'>
                    <img className='Aboutme-Photo' src={Me2} alt="Daniel Tborsk" loading="lazy"/>
                </div>
            </div>
        </div>
    )
} 

export default Aboutme;