import "./Portfolio.css";
import PortfolioCard from "./PortfolioCard";

import Rideaway from "../Assets/rideaway.png";
import Carentab from "../Assets/carentab.png";
import Icon from "../Assets/icon.png";

const Portfolio = () => {
    return(
        <div className="Portfolio" id="Portfolio">
            <p className="Portfolio-Title">Portfolio</p>
            <p className="Portfolio-Subtitle">Pokud Vs m prce zaujala, kontaktujte m a domluvme se na dal spoluprci.</p>
            <div className="Portfolio-Cards">
                <PortfolioCard 
                    Img={Rideaway} 
                    Title={'Rideaway-moto.cz'} 
                    Description={'Pjovna motocykl Klterec nad Oh'}
                    Web={'https://rideaway-moto.cz/'}
                    Github={'https://github.com/DanielTaborsky77/Rideaway-moto.cz'}
                    Details={[
                        'Web je vytvoen pro pjovnu motocykl v Klterci nad oh. ', 
                        'Jeho elem je zkaznky informovat o spolenosti, pjovanch motocyklech (vbava, cena, vzhled - fotogalerie), a hlavn dt zkaznkovi monost si motorku zarezervovat.',
                        'Jedn se o jednostrnkov web, na kterm jsou pouity prvky z HTML5, CSS3, JS, a PHP. Klov je ji zmnn PHP formul pro rezervaci motocykl.',
                        'Web je pln responzivn na vechny typy zazen. '
                    ]}
                />

                <PortfolioCard 
                    Img={Carentab} 
                    Title={'Carentab.com'} 
                    Description={'Pjovna automobil Karlovarskho kraje'}
                    Web={'https://carentab.com/'}
                    Github={'https://github.com/DanielTaborsky77/Carentab.com'}
                    Details={[
                        'Web je vytvoen pro pjovnu automobil CARENTAB v Karlovarskm Kraji. ', 
                        'Jeho elem je zkaznky informovat o spolenosti, pjovanch vozidlech (vbava, cena, vzhled - fotogalerie), a hlavn dt zkaznkovi monost si vozidlo zarezervovat.',
                        'Jedn se o jednostrnkov web, na kterm jsou pouity prvky z React.js, CSS, JS a PHP. Klov je ji zmnn formul pro rezervaci vozidel, kter zpracovv a odesl PHP API.',
                        'Web je pln responzivn na mobiln, desktopov a dal zazen. '
                    ]}
                />
                
                <PortfolioCard 
                    Img={Icon} 
                    Title={'ITaborsky.cz'} 
                    Description={'Osobn web s portfoliem a nabdkou slueb'}
                    Web={'https://itaborsky.cz/'}
                    Github={'https://github.com/DanielTaborsky77/ITaborsky.cz'}
                    Details={[
                        'Web je uren pro mou osobn prezentaci a ukzku mho portfolia.', 
                        'Strnka navc obsahuje jednotliv sluby (a jak jednotliv probhaj), kter mohu potencionlnm zkaznkm nabdnout. Nechyb monost si o slubu nezvazn zadat pomoc formule, kter odesl e-maily pomoc JS API nahran ve Firebase functions.',
                        'Aplikace je postavena na JS Frameworku Node.js a React.js. Prvky HTML a stylovn CSS jsou samozejmost.', 
                        'Strnka je mimo jin tak responzivn na vechny typy zazen.'
                    ]}
                />
            </div>
        </div>
    )
}

export default Portfolio;